import React, { createContext } from 'react'
import { useStaticQuery, graphql } from 'gatsby'

export const ServicesContext = createContext({
    services: [],
    wave: null
})

const ServiceProvider = ({ children }) => {
    const servicesData = useStaticQuery(graphql`
        query{
            images: allFile(filter: {relativeDirectory: {eq: "services"}}) {
            nodes {
                childImageSharp {
                fluid {
                    ...GatsbyImageSharpFluid_withWebp
                    originalName
                }
                }
            }
            }
            nechtyImages: allFile(filter: {relativeDirectory: {eq: "nechty"}}) {
                nodes {
                childImageSharp {
                    fluid {
                    ...GatsbyImageSharpFluid_withWebp
                    }
                }
                }
            }
            makeupImages: allFile(filter: {relativeDirectory: {eq: "makeup"}}) {
            nodes {
                childImageSharp {
                fluid {
                    ...GatsbyImageSharpFluid_withWebp
                }
                }
            }
            }
            kozmetikaImages: allFile(filter: {relativeDirectory: {eq: "kozmetika"}}) {
            nodes {
                childImageSharp {
                fluid {
                    ...GatsbyImageSharpFluid_withWebp
                }
                }
            }
            } 

            vlnkaImage: allImageSharp(filter: {fluid: {originalName: {eq: "vlnka.png"}}}) {
                nodes {
                fluid {
                    ...GatsbyImageSharpFluid_withWebp
                }
                }
  }
        }
  `)

    const wave = servicesData.vlnkaImage.nodes[0].fluid


    const services = [
        {
            id: 'nechty',
            img: servicesData.images.nodes.find(img => img.childImageSharp.fluid.originalName === "nechty.png"),
            title: "Manikra",
            heading: "Manikra",
            desc: `Zverte svoje ruky do rk odbornkov, i u klasick manikra, lakovanie, glov i akrylov nechty a gl-lak. Obrovsk vber farieb, kvalitn materil a profesionlny prstup Vm zaru, e Vae nechty mu vyzera dokonale a po dobu tyroch tdov. Radi Vs privtame, prdite sa k nm presvedi.`,
            references: servicesData.nechtyImages.nodes,
            pricing: {
                title: "Manikra",
                additional: '',

                services: [
                    {
                        name: 'Klasick manikra',
                        price: "15"
                    },
                    {
                        name: 'Odlakovanie',
                        price: "2"
                    },
                    {
                        name: 'Lakovanie farebnm lakom',
                        price: "4"
                    },
                    {
                        name: 'Lakovanie gl-lakom',
                        price: "20"
                    },
                    {
                        name: 'Odstrnenie gl-laku',
                        price: "10"
                    },
                    {
                        name: 'Gelov/akrylove nechty - nov',
                        price: "35-38"
                    },
                    {
                        name: 'Gelov/akrylov nechty  doplnenie',
                        price: "25"
                    },
                    {
                        name: 'Gelov/akrylov nechty  pecilne zdobenie',
                        price: "5-10"
                    },
                    {
                        name: 'Vytvrdenie prrodnch nechtov glom',
                        price: "25"
                    },
                    {
                        name: 'Odstrnenie glovch/akrylovch nechtov',
                        price: "15"
                    },
                ]
            }
        },
        {
            id: 'kozmetika',
            img: servicesData.images.nodes.find(img => img.childImageSharp.fluid.originalName === "kozmetika.png"),
            title: "Kozmetika",
            heading: "Kozmetika",
            desc: `Bute nron, jedinen, krsna a vzcna tak ako naa pokoka. Ponkame Vm prstrojov kozmetick oetrenia it na mieru s Exkluzvne registrovanou obchodnou znakou DIEGO DALLA PALMA, ktor v sebe spja typick taliansky pvab a spoahlivos vedeckho vskumu. Profesionlny prstup, relax, prjemn atmosfra, nik s kadodennho stereotypu - to vetko a omnoho viac Vs ak v prjemnom prostred nho tdia. Doprajte si pocit jedinenosti prve u Ns.`,
            references: servicesData.kozmetikaImages.nodes,

            pricing: {
                title: "Kozmetika",
                additional: '',
                services: [
                    {
                        name: "Zkladn istenie pleti ultrazvukom",
                        price: "30"
                    },
                    {
                        name: "Galvanizcia",
                        price: "10"
                    },
                    {
                        name: "Mas tvre, krku a dekoltu",
                        price: "13"
                    },
                    {
                        name: "Mas tvre, krku a dekoltu (preden)",
                        price: "20"
                    },
                    {
                        name: "Kompletn oetrenie pleti + /rdiofrekvencia/",
                        price: "50"
                    },
                    {
                        name: "Kompletn oetrenie pleti + /rdiofrekvencia,mas/",
                        price: "55"
                    },
                    {
                        name: "Hydropeel (istenie vodou)",
                        price: "38"
                    },
                    {
                        name: "Diamantov mikrodermabrzia",
                        price: "25"
                    },
                    {
                        name: "Rdiofrekvencia /tvr + maska/",
                        price: "28"
                    },
                    {
                        name: "Rdiofrekvencia /tvr, krk + maska/",
                        price: "40"
                    },
                    {
                        name: "Rdiofrekvencia  /tvr, krk, dekolt + maska/",
                        price: "50"
                    },
                    {
                        name: "Dermabotoxn oetrenie",
                        price: "35"
                    },
                    {
                        name: "Chemick peeling /tvr/",
                        price: "30"
                    },
                    {
                        name: "Chemick peeling /dekolt/",
                        price: "10"
                    },
                    {
                        name: "Chemick peeling /chrbt/",
                        price: "15"
                    },
                    {
                        name: "prava oboia",
                        price: "3"
                    },
                    {
                        name: "Farbenie oboia",
                        price: "3"
                    },
                    {
                        name: "Farbenie rias",
                        price: "3"
                    },
                ]
            }
        },
        {
            id: 'make-up',
            img: servicesData.images.nodes.find(img => img.childImageSharp.fluid.originalName === "make-up.png"),
            title: "Make Up",
            heading: "Permanentn Make-up (Pmu)",
            desc: `Je skvelou pomckou hlavne pre tie z Vs, ktor u nebav kadodenn lenie i u oboia, liniek alebo pier. Pri profesionlnom preveden etr as a zvrazn pekn rty tvare. Dnes u je v ponuke nielen monos vberu vysokokvalitnch pigmentov ,ale rovnako tak aj monos vberu v technike Pmu. Naprklad microblading s 3D efektom, ktor relne napodobuje chpky oboia, alebo pdrov i podtieovan oboie, poprpade kombinovan technika.
Zverte sa do rk profesionlov a zobdzajte sa kad rno dokonale upraven.`,
            references: servicesData.makeupImages.nodes,
            pricing: {
                title: "Viz",
                additional: "Tetovanie - Cena zle od konenho motvu, vekosti, nronosti, farebnosti. Zkladn cena u ns je od 40.",
                services: [
                    {
                        name: "Denn lenie",
                        price: '20'
                    },
                    {
                        name: "Veern lenie (svadobn)",
                        price: '35'
                    },
                    {
                        name: "Veern lenie (svadobn) + trsy",
                        price: '40'
                    },
                    {
                        name: "Permanentn MakeUp  pdrov oboie (vrtane korekcie)",
                        price: '160'
                    },
                    {
                        name: "Permanentn MakeUp  iarkov oboie vlskovanm (vrtane korekcie)",
                        price: '180'
                    },
                    {
                        name: "Permanentn MakeUp  kombinovan technika (vrtane korekcie)",
                        price: '200'
                    },
                    {
                        name: "Permanentn MakeUp  on linky (vrtane korekcie)",
                        price: '100'
                    },
                    {
                        name: "Permanentn MakeUp  Microblading",
                        price: '100'
                    },
                    {
                        name: "Permanentn MakeUp  Korekcia",
                        price: '20'
                    },
                    {
                        name: "Permanentn MakeUp  Pery (pln vpl)",
                        price: '150'
                    },
                ]
            }
        },
        {
            id: 'kadernictvo',
            img: servicesData.images.nodes.find(img => img.childImageSharp.fluid.originalName === "kadernictvo.png"),
            title: "Kadernctvo",
            heading: "",
            desc: "Pripravujeme"
        },
        {
            id: 'pedikura',
            img: servicesData.images.nodes.find(img => img.childImageSharp.fluid.originalName === "pedikura.png"),
            title: "Pedikra",
            heading: "",
            desc: "Pripravujeme"
        }
    ]

    return (
        <ServicesContext.Provider value={{
            services,
            wave
        }}>
            {children}
        </ServicesContext.Provider>
    )
}

export default ServiceProvider