import React from 'react'

import './service-pricing.styles.scss'

const ServicePricing = ({ pricing: { title, services = [], additional } }) => {

    return (
        <div id='cennik' className='service-pricing'>
            <div style={{ height: '8rem' }} />

            <div className='pricing-table'>
                <div className='head'>
                    <h3>{title}</h3>
                </div>

                {
                    services.map((service, idx) => (
                        <div key={idx} className='price-row'>
                            <div>{service.name}</div>
                            <div>{service.price}</div>
                        </div>
                    ))
                }
            </div>

            <p className='additional'>{additional}</p>

            {title === "Viz" && (<div className='pricing-table'>
                <div className='head'>
                    <h3>Depilcia</h3>
                </div>

                <div className='price-row'>
                    <div>
                        Depilcia brady
                    </div>
                    <div>
                        3
                    </div>
                </div>
                <div className='price-row'>
                    <div>
                        Depilcia hornej pery
                    </div>
                    <div>
                        2
                    </div>
                </div>
                <div className='price-row'>
                    <div>
                        Depilcia predlaktia
                    </div>
                    <div>
                        8
                    </div>
                </div>
                <div className='price-row'>
                    <div>
                        Depilcia celch rk
                    </div>
                    <div>
                        11
                    </div>
                </div>
                <div className='price-row'>
                    <div>
                        Depilcia podpazuia
                    </div>
                    <div>
                        8
                    </div>
                </div>
                <div className='price-row'>
                    <div>
                        Depilcia predkolenia
                    </div>
                    <div>
                        11
                    </div>
                </div>
                <div className='price-row'>
                    <div>
                        Depilcia celch nh
                    </div>
                    <div>
                        16
                    </div>
                </div>
                <div className='price-row'>
                    <div>
                        Depilcia chrbta
                    </div>
                    <div>
                        8
                    </div>
                </div>
            </div>)}
        </div>
    )
}

export default ServicePricing
